<template>
  <div style="margin-top:15px;font-size:0.8rem;position:fixed;top:50px;bottom:0px;left:0px;right:0px;overflow-y:scroll;color:#000;">
    <div>
      <!-- 电费方式 -->
      <p class="life_box"
         v-if="crrent_info.ammeter == 1"></p>
      <div class="rent_box"
           v-if="crrent_info.ammeter == 1">
        <div>电费方式</div>
        <div v-for="(item,i) in bothInfo"
             :key="i">
          <div class="rent_right"
               @click="eleStyle(i)">
            <img v-if="item.statu==false"
                 src="@/assets/btn_normal.png"
                 alt />
            <img v-else
                 src="@/assets/btn_selected.png"
                 alt />
            <p>{{ item.name }}</p>
          </div>
        </div>
      </div>
      <p class="life_box"></p>
      <div class="live_p">
        <p>生活缴费</p>
        <img class="img_add_delete w20"
             src="@/assets/five/add.png"
             alt
             @click="addLifePay" />
      </div>
      <!-- 费用项目 -->
      <div class="local_info">
        <span>费用项目</span>
        <span class="w30">价格</span>
        <span class="w50">初始读数</span>
      </div>
      <!-- 费用列表 -->
      <div v-if="lifeCost.length > 0">
        <table v-for="(item,i) in lifeCost"
               :key="i"
               class="info_box">
          <tr>
            <td>
              <div>
                <!-- <img src="@/assets/two/remove.png" alt /> -->
                <img @click="remove(item)"
                     class="img_add_delete pr4"
                     src="@/assets/five/delete.png"
                     alt />
                <span>{{item.dictLabel}}</span>
              </div>
            </td>
            <td style="-border:1px solid red;width:44%;display: flex;align-items: center;">
              <input class="input_box f14"
                     type="number"
                     step="1"
                     min="0"
                     @keyup="clearNoNum(item)"
                     placeholder="请输入"
                     v-model="item.price" />
              <span class="f14 cb2">{{ unitList[item.dictSort] }}</span>
            </td>
            <td v-if="item.showlength != -1"
                @click="setPrice(item)"
                class="f14">
              <span :style="{color:(item.showlength == item.allRoom)?'#02BC6C':'#FF1D1D'}">{{ item.showlength == item.allRoom?'已完成':'未完成' }}</span>
              <span>{{ item.showlength  }}/ {{ item.allRoom }}</span>
              <van-icon name="arrow"
                        class="cb2" />
            </td>
            <td v-else></td>
          </tr>
        </table>
      </div>
      <!-- 完成办理 -->
      <mt-button class="button"
                 v-if='readingStatus == false'
                 @click="save">保存</mt-button>
      <!-- 添加 -->
      <mt-popup v-model="add_box"
                position="bottom"
                style="width:100%;">
        <div v-for="(item,i) in lastCost"
             :key="i">
          <p @click="choose_item(item)"
             style="line-height:50px;border-bottom:1px solid #ebebeb;">{{ item.dictLabel }}</p>
        </div>
      </mt-popup>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessCooperLifeCostBillBycooperorderroom } from '@/api/api_business';
let dictValueObj = {
    elecPrice: 1,
    waterPrice: 2,
    gasPrice: 3
};
export default {
    data() {
        return {
            price: '',
            first: '查看',
            bothInfo: [{ name: '后付费', statu: true }, { name: '预付款', statu: false }],
            localInfo: ['费用项目', '价格', '初始读数'],
            lifeCost: [],
            add_box: false,
            lastCost: [],
            crrent_info: '',
            elecPayModel: '',
            foreverCost: [],
            unitList: {
                1: '元/度',
                2: '元/吨',
                3: '元/m³',
                4: '元/月',
                5: '元/月',
                6: '元/月',
                7: '元/月'
            },
            readingStatus: true
        };
    },
    created() {
        this.$store.commit('setHeader', '费用设置');
        this.getMore();
    },
    methods: {
        clearNoNum(item) {
            let value = item.price;
            value = value.toString();
            value = value.replace(/[^\d.]/g, ''); //清除“数字”和“.”以外的字符
            value = value.replace(/\.{2,}/g, '.'); //只保留第一个. 清除多余的
            value = value
                .replace('.', '$#$')
                .replace(/\./g, '')
                .replace('$#$', '.');
            value = value.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //只能输入两个小数
            if (value.indexOf('.') < 0 && value != '') {
                value = parseFloat(value);
            }
            item.price = value;
        },
        getMore() {
            GetSystemDictDataByType({ dictType: 'business_life_cost' }).done(async (res) => {
                let retArray = await GetBusinessCooperLifeCostBillBycooperorderroom({ cooperOrderRoomId: this.$route.query.cooperOrderRoomId });
                if (retArray.data.length == 0) {
                    return false;
                }
                this.readingStatus = retArray.data.some((item) => item.status == 1);
                retArray = retArray.data;
                let retObj = retArray[0];
                let _list = res.data.map((item) => {
                    item.price = retObj[item.dictValue];
                    switch (item.dictValue) {
                        case 'elecPrice': {
                            item.allRoom = retArray.filter((item) => item.elecPrice > 0).length;
                            break;
                        }
                        case 'waterPrice': {
                            item.allRoom = retArray.filter((item) => item.waterPrice > 0).length;
                            break;
                        }
                        case 'gasPrice': {
                            item.allRoom = retArray.filter((item) => item.gasPrice > 0).length;
                            break;
                        }
                        default: {
                            item.showlength = -1;
                            break;
                        }
                    }
                    item.showlength = 0;
                    switch (item.dictValue) {
                        case 'elecPrice': {
                            item.showlength = retArray.filter((item) => item.elecPrice > 0 && item.preElecDegree != null).length;
                            break;
                        }
                        case 'waterPrice': {
                            item.showlength = retArray.filter((item) => item.waterPrice > 0 && item.preWaterDegree != null).length;
                            break;
                        }
                        case 'gasPrice': {
                            item.showlength = retArray.filter((item) => item.gasPrice > 0 && item.preGasDegree != null).length;
                            break;
                        }
                        default: {
                            item.showlength = -1;
                            break;
                        }
                    }
                    return item;
                });
                this.foreverCost = _list; // result.data;
                this.lifeCost = _list; //result.data
            });
        },
        // 设置初始读数
        setPrice(item) {
            this.$router.push({
                path: `/business-set-reading`,
                query: {
                    dictLabel: item.dictLabel,
                    dictValue: dictValueObj[item.dictValue],
                    cooperOrderId: this.$route.query.cooperOrderId,
                    cooperLifeCostBillId: this.$route.query.cooperLifeCostBillId
                }
            });
        },
        // 电费方式
        eleStyle(i) {
            this.elecPayModel = i;
            if (i == 0) {
                this.bothInfo[0].statu = true;
                this.bothInfo[1].statu = false;
            } else {
                this.bothInfo[0].statu = false;
                this.bothInfo[1].statu = true;
            }
        },
        // 删除
        remove(item) {
            let remove_id = item.id;
            this.lifeCost = this.lifeCost.filter((item) => item.id != remove_id);
        },
        // 添加-生活缴费
        addLifePay() {
            if (this.foreverCost.length > 0) {
                this.lastCost = this.foreverCost.filter((v) => !this.lifeCost.includes(v));
                for (var i = 0; i < this.lastCost.length; i++) {
                    this.lastCost[i].dictPrice = '';
                }
            }
            if (this.lastCost.length == 0) {
                Toast({
                    message: '暂无可添加',
                    duration: 900
                });
                return false;
            } else {
                this.add_box = true;
            }
        },
        // 选择
        choose_item(item) {
            for (var k in this.otherAmount) {
                if (k == item.dictValue) {
                    item.dictPrice = this.otherAmount[k];
                }
            }
            this.lifeCost = [...this.lifeCost, item];
            this.add_box = false;
        },
        // 保存
        save() {
            let obj = {};
            this.lifeCost.forEach((item) => {
                obj[item.dictValue] = item.price;
            });
            let data = {
                cooperLifeCostBillId: this.$route.query.cooperLifeCostBillId,
                ...obj
            };
        }
    },
    watch: {}
};
</script>
<style scoped>
.c_88 {
    color: #888;
}
.life_box {
    font-size: 12px;
    color: #9b9b9b;
    text-align: left;
    padding-left: 20px;
    border-bottom: 1px solid #ebebeb;
    line-height: 30px;
    background-color: #f6f6f6;
    padding-top: 10px;
}
.rent_box {
    background: #fff;
    display: flex;
    justify-content: space-between;
    padding: 14px 20px;
    align-items: center;
    border-bottom: 1px solid #ebebeb;
}
.rent_right {
    display: flex;
    align-items: center;
    font-size: 0.6rem;
}
.rent_right > img {
    width: 16px;
    height: 16px;
    padding-right: 4px;
}
.live_p {
    display: flex;
    justify-content: space-between;
    text-align: left;
    padding: 14px 20px;
    background: #fff;
}
.local_info {
    display: flex;
    padding: 14px 20px;
    justify-content: space-between;
}
.w30 {
    display: inline-block;
    text-align: left;
}
.w50 {
    display: inline-block;
    width: 28%;
    text-align: left;
}
.info_box {
    background: #fff;
    border-bottom: 1px solid #e5e5e5;
    padding: 14px 20px;
    width: 100%;
}
.info_box > tr {
    display: flex;
}
.info_box > tr > td:nth-child(1) {
    display: flex;
    width: 46%;
}
.img_add_delete {
    width: 16px;
    vertical-align: text-bottom;
}
.w20 {
    width: 1rem;
    height: 1rem;
}
.pr4 {
    padding-right: 4px;
}
.pl10 {
    padding-left: 16%;
}
.f14 {
    font-size: 0.7rem;
}
.cb2 {
    color: #b2b2b2;
}
.info_box > tr > td {
    display: flex;
    align-items: center;
}
.info_box > tr > td:nth-child(3) {
    width: 36%;
    text-align: right;
}
.input_box {
    width: 34%;
}
.button {
    width: 100%;
    background: #ff7d41;
    opacity: 0.9;
    color: #fff;
    font-size: 0.8rem;
    margin: 20px 0;
    height: 50px;
}
</style>



